// Here you can create and customize the pages' header (Navigation):

@use "../globals" as *;
@use "../helpers" as *;

%header {
  position: absolute;
  z-index: 2;
  width: 100%;
  @extend %box-shadow;
  border-bottom: 1px solid #e9e6e6;
  background-color: $white;
  .container {
    display: flex;
    flex-direction: column;
    position: relative;
    @include breakpoint(tablet) {
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
    }
    h1 a {
      display: block;
      font-size: 26px;
      text-align: center;
      margin: 10px auto;
      color: $main-clr;
    }
    .main_nav {
      display: flex;
      justify-content: space-evenly;
      flex-wrap: wrap;
      > li {
        > a {
          display: block;
          position: relative;
          font-size: 14px;
          padding: 10px;
          overflow: hidden;
          color: $black;
          transition: $transition;
          &::before {
            content: "";
            position: absolute;
            top: 0;
            left: -100%;
            height: 4px;
            width: 100%;
            transition: $transition;
            background-color: $main-clr;
          }
          &:hover::before {
            left: 0;
          }
          &:hover {
            color: $main-clr;
            background-color: $transparent-clr;
          }
          @include breakpoint(tablet) {
            font-size: 18px;
            padding: 20px 30px;
          }
        }
      }
      .megamenu {
        display: none;
        position: absolute;
        width: 100%;
        left: 0;
        top: 102%;
        transition: $transition;
        border-bottom: 3px solid $main-clr;
        background-color: white;
        @include breakpoint(tablet) {
          padding: 20px;
        }
        // @include breakpoint(laptop) {
        //   display: flex;
        //   column-gap: 30px;
        // }
        img {
          width: 450px;
          @media (max-width: 991px) {
            display: none;
          }
        }
        .sec_nav {
          flex: 1;
          a {
            display: block;
            position: relative;
            z-index: 2;
            font-weight: bold;
            padding: 15px 10px;
            border-bottom: 1px solid #e9e6e6;
            color: $main-clr;
            &::before {
              content: "";
              position: absolute;
              z-index: -1;
              top: 0;
              left: 0;
              width: 0;
              height: 100%;
              transition: $transition;
              background-color: #fafafa;
            }
            &:hover::before {
              width: 100%;
            }
          }
          i {
            margin-right: 15px;
            font-size: 20px;
          }
        }
      }
    }
  }
}
