// Here you can create and customize the pages' footer:

@use "../globals" as *;
@use "../helpers" as *;
@use "../mixins" as *;

%footer {
  padding-top: 50px;
  background-color: #191919;
  .container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 40px;
    .box {
      text-align: center;
      h2 {
        color: white;
        font-size: 50px;
      }
      ul {
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 50px 0;
        li {
          margin: 0 10px;
          a {
            color: #b9b9b9;
            padding: 5px 10px;
            background-color: #313131;
          }
        }
      }
      p {
        line-height: 2;
        color: #b9b9b9;
      }
    }
    .links {
      width: 100%;
      text-align: center;
      li {
        position: relative;
        padding: 10px 0;
        transition: $transition;
        border-bottom: 1px solid #444;
        &::before {
          content: "\F101";
          font-family: "Font Awesome 5 Free";
          font-weight: 900;
          margin-right: 10px;
          color: $main-clr;
        }
        &:hover {
          padding-left: 15px;
          a {
            color: white;
          }
        }
        a {
          transition: $transition;
          color: #b9b9b9;
        }
      }
    }
    .info {
      text-align: center;
      li {
        display: flex;
        align-items: center;
        flex-direction: column;
        margin-bottom: 20px;
        @include breakpoint(tablet) {
          flex-direction: row;
          text-align: start;
          i {
            margin: 0 10px !important;
          }

        }
        i {
          font-size: 25px;
          margin-bottom: 10px;
          color: $main-clr;
        }
        span {
          display: block;
          color: #b9b9b9;
        }
      }
    }
    .images {
      text-align: center;
      img {
        width: 75px;
        margin: 4px;
        border: 4px solid white;
      }
    }
  } 
  .copyright {
    font-size: 18px;
    font-weight: bold;
    margin-top: 30px;
    padding: 20px 0;
    text-align: center;
    color: #b9b9b9;
    text-transform: capitalize;
    border-top: 1px solid #b9b9b9;
    span:last-of-type {
      color: $main-clr;
    }
  }
}
