// Here You can style and customize the home page:

@use "../globals" as *;
@use "../mixins" as *;
@use "../helpers" as *;
@use "../components" as *;

// <> How to organize the CSS properties:
// 1 - >> display stuff:
// 2 - >> positioning stuff:
// 3 - >> box-model stuff:
// 4 - >> typography:
// 5 - >> manipulation:
// 6 - >> misc:

// --------------- start header Section ---------------//
header {
  @extend %header;
}
/* --------------- End header Section ------------------*/

/* -------------- Start Landing section ----------------*/
.landing {
  height: 100vh;
  position: relative;
  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -2;
    background-color: $section-bg-clr;
    clip-path: polygon(100% 0, 100% 85%, 0 97%, 0 0);
  }
  .container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    position: relative;
    height: 100%;
    @include breakpoint(laptop) {
      flex-direction: row;
      justify-content: space-between;
    }
    .text {
      max-width: 500px;
      margin-bottom: 50px;
      h2 {
        font-size: 25px;
        letter-spacing: -1px;
        text-transform: capitalize;
      }
      p {
        font-size: 18px;
        line-height: $line-height;
        color: $txt-clr;
      }
      @include breakpoint(laptop) {
        flex-basis: 40%;
        text-align: start;
        margin-bottom: 0;
        h2 { 
          font-size: 40px;
        }
        p {
          font-size: 25px;
        }
      }
    }
    img {
      position: relative;
      width: 80%;
      animation: up-and-down 5s linear infinite;
      @include breakpoint(laptop) {
        flex-basis: 60%;
      }
    }
    i {
      position: absolute;
      color: $main-clr;
      bottom: 50px;
      left: 50%;
      transform: translateX(-50%);
      animation: bouncing 1.5s linear infinite;
      &:hover {
        color: $main-clr-alt;
      }
    }
  }
}

/* --------------- End Landing section -----------------*/

/* -------------- Start Articles section ---------------*/
.articles {
  padding-top: $section-padding;
  padding-bottom: $section-padding;
  .container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 40px;
  }
  .main_title {
    @extend %main_title;
  }
  figure {
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0 5px 5px #cecece;
    transition: $transition;
    &:hover {
      box-shadow: 0 10px 10px #cecece;
      transform: translateY(-10px);
      i {
        animation: moving-arrow 0.8s linear infinite;
      }
    }
    figcaption {
      padding: 0 15px;
      h3 {
        text-transform: capitalize;
        margin: 10px 0;
      }
      p {
        color: $txt-clr;
        margin-bottom: 10px;
      }
      a {
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-weight: bold;
        padding: 10px 0;
        text-transform: capitalize;
        border-top: 1px solid #e6e6e7;
        color: $main-clr;
        i {
          position: relative;
          margin-right: 5px;
        }
      }
    }
  }
}

/* --------------- End Articles section ---------------*/

/* --------------- Start Gellary section --------------*/
.gallery {
  padding-top: $section-padding;
  padding-bottom: $section-padding;
  background-color: $section-bg-clr;
  .main_title {
    @extend %main_title;
  }
  .container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 40px;
    .image {
      position: relative;
      overflow: hidden;
      border: 10px solid white;
      box-shadow: 0 5px 5px #cecece;
      background-color: #fff;
      &::after {
        content: "";
        position: absolute;
        z-index: 2;
        width: 0;
        height: 0;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background-color: rgba(255, 255, 255, 20%);
      }
      img {
        transition: $transition;
      }
      &:hover {
        img {
          transform: rotate(5deg) scale(1.1);
        }
        ::after {
          animation: flashing 0.7s linear forwards;
        }
      }
    }
  }
}

/* ---------------- End Gellary section --------------*/

/* --------------- Start Features Section ------------*/
.features {
  padding-top: $section-padding;
  padding-bottom: $section-padding;
  .main_title {
    @extend %main_title;
  }
  .container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 40px;
  }

  .quality {
    .img-holder::before {
      background-color: #f43f3660;
    }
    h3::after {
      background-color: #f44036;
    }
    a {
      border-color: #f44036;
      color: #f44036;
      &::before {
        background-color: #f44036;
      }
    }
  }

  .time {
    .img-holder::before {
      background-color: #00968860;
    }
    h3::after {
      background-color: #009688;
    }
    a {
      border-color: #009688;
      color: #009688;
      &::before {
        background-color: #009688;
      }
    }
  }

  .passion {
    .img-holder::before {
      background-color: #03a9f460;
    }
    h3::after {
      background-color: #03a9f4;
    }
    a {
      border-color: #03a9f4;
      color: #03a9f4;
      &::before {
        background-color: #03a9f4;
      }
    }
  }

  figure {
    border: 1px solid #cecece;
    &:hover a::before {
      width: 100%;
    }
    &:hover a {
      color: white;
    }
    &:hover .img-holder::after {
      border-width: 170px 500px 170px 0;
    }
    .img-holder {
      position: relative;
      overflow: hidden;
      &::before {
        // @extend %overlay;
        content: "";
        position: absolute;
        height: 100%;
        width: 100%;
        left: 0;
        top: 0;
      }
      &::after {
        content: "";
        position: absolute;
        right: 0;
        bottom: 0;
        transition: $transition;
        border-style: solid;
        border-width: 0 0 170px 500px;
        border-color: transparent transparent white transparent;
      }
    }
    figcaption {
      text-align: center;
      padding: 30px 20px;
      h3 {
        position: relative;
        font-size: 40px;
        margin: auto;
        width: fit-content;
        text-transform: capitalize;
        &::after {
          content: "";
          position: absolute;
          height: 5px;
          width: calc(100% - 30px);
          left: 15px;
          bottom: -15px;
        }
      }
      p {
        line-height: $line-height;
        color: $txt-clr;
        font-size: 20px;
        margin: 40px 0;
      }
      a {
        display: block;
        position: relative;
        width: fit-content;
        font-size: 20px;
        font-weight: bold;
        margin: 0 auto;
        padding: 5px 25px;
        border-radius: 5px;
        text-transform: capitalize;
        border: 4px solid transparent;
        transition: $transition;
        &::before {
          content: "";
          position: absolute;
          width: 0%;
          height: 100%;
          top: 0;
          left: 0;
          z-index: -1;
          transition: $transition;
        }
      }
    }
  }
}

/* ---------------- End Features Section -------------*/

/* -------------- Start Testimonial Section -----------*/
.testimonial {
  padding-top: $section-padding;
  padding-bottom: $section-padding;
  background-color: $section-bg-clr;
  .main_title {
    @extend %main_title;
  }
  .container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    row-gap: 75px;
    column-gap: 40px;
  }
  figure {
    position: relative;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0 5px 5px #cecece;
    background-color: #fff;
    img {
      position: absolute;
      top: -25%;
      right: -10px;
      width: 100px;
      border-radius: 50%;
      border: 10px solid $section-bg-clr;
    }
    span {
      display: block;
      margin: 5px 0;
      color: $txt-clr;
    }
    i:not(:last-of-type) {
      color: #ffc107;
    }
    p {
      color: $txt-clr;
    }
  }
}

/* --------------- End Testimonial Section ------------*/

/* ---------------- Start Members Section -------------*/
.members {
  padding-top: $section-padding;
  padding-bottom: $section-padding;
  .main_title {
    @extend %main_title;
  }
  .container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    row-gap: 75px;
    column-gap: 40px;
  }
  .box {
    position: relative;
    padding-top: 50px;
    &::before {
      content: "";
      position: absolute;
      width: calc(100% - 60px);
      height: 100%;
      top: 0;
      right: 0;
      z-index: -1;
      border-radius: 10px;
      box-shadow: 0 5px 5px #cecece;
      background-color: $section-bg-clr;
    }
    &::after {
      content: "";
      position: absolute;
      width: 0%;
      height: 100%;
      top: 0;
      right: 0;
      z-index: -1;
      border-radius: 10px;
      transition: $transition;
      background-color: #e4e4e4;
    }
    &:hover {
      &::after {
        width: calc(100% - 60px);
      }
      img {
        filter: grayscale(100%);
      }
      h3 {
        color: $txt-clr; //this code is not working!!
      }
    }
    .content {
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        border-radius: 10px;
        transition: $transition;
        width: calc(100% - 60px);
      }
      .social_media {
        display: flex;
        flex-direction: column;
        row-gap: 20px;
        justify-content: center;
        align-items: center;
        flex: 1;
        i {
          padding: 10px;
          color: $txt-clr;
          &:hover {
            color: $main-clr;
          }
        }
      }
    }
    .desc {
      margin: 20px 0 15px 75px;
      h3 {
        font-size: 20px;
        color: $main-clr;
      }
    }
  }
}

/* ----------------- End Members Section --------------*/

/* ---------------- Start Services Section ------------*/
.services {
  padding-top: $section-padding;
  padding-bottom: $section-padding;
  background-color: $section-bg-clr;
  .main_title {
    @extend %main_title;
  }
  .container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    row-gap: 75px;
    column-gap: 40px;
  }
  .srv {
    position: relative;
    transition: $transition;
    box-shadow: 0 5px 5px #cecece;
    background-color: #fff;
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      height: 3px;
      width: 0;
      transition: $transition;
      background-color: $main-clr;
    }
    &:hover::after {
      width: 100%;
    }
    &:hover {
      transform: translateY(-10px);
    }
    .name {
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: center;
      padding: 30px 0;
      i {
        color: #d5d5d5;
      }
      h3 {
        font-size: 25px;
        margin-top: 10px;
        text-transform: capitalize;
        color: $main-clr;
      }
    }
    .details {
      display: flex;
      align-items: center;
      justify-content: space-between;
      position: relative;
      overflow: hidden;
      padding: 0 10px;
      background-color: #f9f9f9;
      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: -15px;
        height: 100%;
        width: 90px;
        transform: skewX(-28.6deg);
        background-color: $main-clr;
        border-right: 40px solid #d5d5d5;
      }
      span {
        position: relative;
        z-index: 2;
        font-size: 30px;
        font-weight: bold;
        padding: 0 10px;
        color: #fff;
      }
      a {
        text-transform: capitalize;
        color: $main-clr;
      }
    }
  }
}

/* ----------------- End Services Section -------------*/

/* ----------------- Start Skills Section -------------*/
.our-skills {
  padding-top: $section-padding;
  padding-bottom: $section-padding;
  .main_title {
    @extend %main_title;
  }
  .container {
    @include breakpoint(laptop) {
      display: flex;
    }
  }
  .img-holder {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .skills {
    flex: 1;
  }
  .skill {
    margin-bottom: 30px;
    h3 {
      margin-bottom: 10px;
      text-transform: uppercase;
    }
    .progress {
      position: relative;
      height: 30px;
      width: 100%;
      background-color: #d5d5d5;
      &::after {
        content: attr(data-count);
        position: absolute;
        top: -30px;
        right: 0;
        font-size: 12px;
        padding: 0 5px;
        color: $main-clr;
        border-radius: 5px;
        border: 1px solid #ccc;
        background-color: white;
      }
      span {
        display: block;
        height: 100%;
        background-color: $main-clr;
      }
    }
  }
}

/* ---------------- End Skills Section --------------*/

/* ------------- Start how it works Section ---------*/
.how-it-works {
  padding-top: $section-padding;
  padding-bottom: $section-padding;
  background-color: $section-bg-clr;
  .main_title {
    @extend %main_title;
  }
  .container {
    @include breakpoint(laptop) {
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 30px;
    }
  }
  .main-img {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .steps {
    flex-basis: 50%;
    .step {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      position: relative;
      padding: 20px;
      margin-top: 30px;
      border-radius: 5px;
      z-index: 1;
      border: 2px solid white;
      background-color: #f6f5f5;
      &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        height: 0;
        width: 0;
        z-index: -1;
        transition: $transition;
        background-color: $section-bg-clr;
      }
      &:hover::before {
        width: 100%;
        height: 100%;
      }
      @include breakpoint(tablet) {
        flex-direction: row;
        gap: 20px;
      }
      .img-holder {
        img {
          width: 75px;
          @include breakpoint(tablet) {
            width: 100px;
          }
          @include breakpoint(laptop) {
            width: 200px;
          }
        }
      }
      .text {
        text-align: center;
        @include breakpoint(tablet) {
          text-align: start;
        }
        h3 {
          font-size: 25px;
          margin: 15px 0;
          text-transform: capitalize;
        }
        p {
          font-size: 18px;
          line-height: $line-height;
          color: $txt-clr;
        }
      }
    }
  }
}
/* -------------- End how it works Section ----------*/

/* ------------ Start Latest Events Section ---------*/
.events {
  padding-top: $section-padding;
  padding-bottom: $section-padding;
  .main_title {
    @extend %main_title;
  }
  .content {
    @include breakpoint(tablet) {
      display: flex;
      align-items: center;
      justify-content: space-between;
      column-gap: 30px;
    }
  }
  img {
    display: block;
    margin: 0 auto 30px;
    width: 75%;
    @include breakpoint(tablet) {
      width: 100%;
    }
  }
  .box {
    flex-basis: 100%;
    .calendar {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      justify-content: center;
      gap: 20px;
      max-width: 500px;
      margin: 0 auto;
      div {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        width: 65px;
        border-radius: 5px;
        transition: $transition;
        border: 1px solid #e6e6e7;
        &:hover {
          border-color: #00e9bd;
          & span:last-of-type {
            border-color: #00e9bd;
          }
        }
      }
      span:first-of-type {
        font-size: 30px;
        font-weight: bold;
        padding: 5px;
        color: #00e9bd;
      }
      span:last-of-type {
        display: block;
        width: 100%;
        font-size: 14px;
        padding: 5px 0;
        text-align: center;
        text-transform: capitalize;
        transition: $transition;
        border-top: 1px solid #e6e6e7;
      }
    }
    .text {
      margin: 50px 0;
      text-align: center;
      h3 {
        font-size: 25px;
        margin-bottom: 20px;
        text-transform: capitalize;
      }
      p {
        font-size: 20px;
        line-height: $line-height;
        color: $txt-clr;
      }
    }
  }
  form {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    padding: 20px;
    height: 150px;
    max-width: 600px;
    border-radius: 10px;
    margin: 30px auto 0;
    background-color: #f6f5f5;
    @include breakpoint(tablet) {
      flex-direction: row;
      height: 100px;
    }
    input:first-of-type {
      width: 100%;
      padding: 10px 20px;
      border: none;
      caret-color: $main-clr;
      border-radius: 20px;
      @include breakpoint(tablet) {
        margin-right: 10px;
      }
    }
    input:last-of-type {
      display: block;
      width: 100%;
      padding: 10px 30px;
      color: white;
      font-weight: bold;
      cursor: pointer;
      border-radius: 20px;
      transition: $transition;
      text-transform: capitalize;
      background-color: $main-clr;
      &:hover {
        background-color: $main-clr-alt;
      }
      @include breakpoint(tablet) {
        width: 150px;
        padding: 16px 0;
        border: none;
      }
    }
  }
}

/* -------------- End Latest Events Section ---------*/

/* ------------- Start Pricing Plans Section --------*/
.pricing-plans {
  padding-top: $section-padding;
  padding-bottom: $section-padding;
  background-color: $section-bg-clr;
  .main_title {
    @extend %main_title;
  }
  .container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 40px;
  }
  .popular {
    position: relative;
    @include breakpoint(desktop) {
      top: -20px;
    }
    > span {
      position: absolute;
      top: 0;
      right: 20px;
      padding: 10px 5px 40px;
      font-size: 18px;
      font-weight: bold;
      writing-mode: tb;
      color: white;
      background-color: $main-clr;
      clip-path: polygon(100% 0, 100% 100%, 46% 90%, 0 100%, 0 0);
    }
  }
  .plan {
    position: relative;
    z-index: 1;
    padding: 20px 0;
    background-color: white;
    &::before,
    &::after {
      content: "";
      position: absolute;
      width: 0;
      height: 50%;
      z-index: -1;
      transition: $transition;
      background-color: #f6f6f6;
    }
    &::before {
      left: 0;
      top: 0;
    }
    &::after {
      right: 0;
      bottom: 0;
    }
    &:hover::before,
    &:hover::after {
      width: 100%;
    }
    .header {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      span:first-of-type {
        font-size: 25px;
        font-weight: bold;
        text-transform: capitalize;
      }
      .img-holder {
        width: 80px;
        margin-top: 15px;
      }
      span:nth-of-type(2) {
        font-size: 60px;
        font-weight: bold;
        color: $main-clr;
      }
      span:last-of-type {
        text-transform: capitalize;
        color: $txt-clr;
      }
    }
    ul {
      margin-top: 20px;
      li {
        position: relative;
        padding: 15px;
        border-top: 1px solid #e6e6e7;
        &::before {
          content: "\f00c";
          font-family: "Font Awesome 5 Free";
          font-weight: bold;
          margin-right: 10px;
          color: $main-clr;
        }
      }
    }
    a {
      font-size: 20px;
      font-weight: bold;
      width: fit-content;
      display: block;
      margin: 15px auto 0;
      padding: 5px 15px;
      transition: $transition;
      border-radius: 5px;
      text-transform: capitalize;
      border: 2px solid $main-clr;
      color: $main-clr;
      &:hover {
        color: white;
        background-color: $main-clr;
      }
    }
  }
}

/* -------------- End Pricing Plans Section ---------*/

/* -------------- Start Top Videos Section ---------*/
.videos {
  padding-top: $section-padding;
  padding-bottom: $section-padding;
  .main_title {
    @extend %main_title;
  }
  .container {
    @include breakpoint(laptop) {
      display: flex;
      justify-content: center;
    }
  }
  .list {
    flex-basis: 50%;
    border: 1px solid $section-bg-clr;
    .name {
      display: flex;
      align-items: center;
      justify-content: space-between;
      font-weight: bold;
      padding: 15px;
      background-color: #f4f4f4;
    }
    ul {
      li {
        padding: 15px;
        cursor: pointer;
        border-top: 1px solid $section-bg-clr;
        transition: $transition;
        &:hover {
          color: $main-clr;
          background-color: #fafafa;
        }
      }
      span {
        display: block;
        margin-top: 5px;
        color: #777;
      }
    }
  }
  .preveiw {
    padding: 10px;
    background-color: $section-bg-clr;
    @include breakpoint(laptop) {
      display: flex;
      justify-content: space-between;
      flex-direction: column;
    }
    .title {
      padding: 15px;
      margin-top: 10px;
      background-color: #fff;
    }
  }
}

/* --------------- End Top Videos Section ----------*/

/* ---------------- Start Stats Section ------------*/
.stats {
  position: relative;
  background-color: #cecece;
  background-image: url("../images/stats.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  padding-top: $section-padding;
  padding-bottom: $section-padding;
  &::before {
    content: "";
    position: absolute;
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
    background-color: rgba(255, 255, 255, 95%);
  }
  .container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 20px;
    position: relative;
  }
  .title {
    position: relative;
    text-align: center;
    font-size: 35px;
    margin-bottom: 80px;
    text-transform: capitalize;
  }
  .content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    position: relative;
    padding: 30px;
    opacity: 0.8;
    transition: $transition;
    background-color: white;
    &::before,
    &::after {
      content: "";
      position: absolute;
      width: 2px;
      height: 0;
      transition: 1s;
      background-color: $main-clr;
    }
    &::before {
      left: 0;
      bottom: 0;
    }
    &::after {
      top: 0;
      right: 0;
    }
    &:hover::before,
    &:hover::after {
      height: 100%;
    }
    &:hover {
      opacity: 1;
    }
    span:first-of-type {
      font-size: 50px;
      font-weight: bold;
    }
    span:last-of-type {
      font-size: 20px;
      font-style: italic;
      font-weight: bold;
      color: $main-clr;
      text-transform: capitalize;
    }
  }
}

/* ----------------- End Stats Section -------------*/

/* --------------- Start Discount Section ----------*/
.discount {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  padding-bottom: $section-padding;
  @include breakpoint(laptop) {
    flex-direction: row;
  }
  .text {
    position: relative;
    flex-basis: 50%;
    padding: 50px 15px 15px;
    text-align: center;
    z-index: 1;
    color: white;
    animation: change-background 20s linear infinite;
    &::before {
      @extend %overlay;
      background-color: #1787e0f7;
    }
    h2 {
      font-size: 35px;
      text-transform: capitalize;
    }
    p {
      margin: 0 auto;
      max-width: 500px;
      font-size: 18px;
    }
    img {
      width: 100%;
      max-width: 500px;
    }
  }
  .field {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    h3 {
      text-align: center;
      font-size: 40px;
      margin: 50px 0;
      letter-spacing: -2px;
      text-transform: capitalize;
    } 
    form {
      display: flex;
      flex-direction: column;
      justify-content: center;
      input {
        @media (min-width: 482px) {
          width: 450px;
        }
      }
      input:not(:last-of-type),
      textarea {
        border: none;
        margin-top: 15px;
        padding: 15px;
        border-bottom: 1px solid #ccc;
        background-color: #f9f9f9;
      }
      input[type="submit"] {
        display: block;
        font-size: 18px;
        font-weight: bold;
        margin: 15px 0;
        padding: 10px 0;
        cursor: pointer;
        color: white;
        background-color: $main-clr;
      }
      textarea {
        resize: none;
        min-height: 200px;
        overflow-y: auto;
      }
    }
  }
}

/* ---------------- End Discount Section -----------*/
/* ---------------- Start Footer section -----------*/
footer {
  @extend %footer;
}
/* ----------------- End Footer section ------------*/
/* ------------------- Start Animation -------------*/
@keyframes up-and-down {
  0%,
  100% {
    top: 0;
  }
  50% {
    top: -50px;
  }
}
@keyframes bouncing {
  0%,
  10%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }
  40%,
  60% {
    transform: translateY(-15px);
  }
}
@keyframes move-left {
  50% {
    height: 12px;
    width: 12px;
    left: 0px;
  }
  100% {
    left: 0px;
    width: 50%;
    height: 100%;
    border-radius: 0;
  }
}
@keyframes move-right {
  50% {
    height: 12px;
    width: 12px;
    right: 0px;
  }
  100% {
    right: 0px;
    width: 50%;
    height: 100%;
    border-radius: 0;
  }
}
@keyframes moving-arrow {
  100% {
    transform: translateX(10px);
  }
}
@keyframes flashing {
  0%,
  40% {
    opacity: 1;
  }
  100% {
    opacity: 1;
    width: 200%;
    height: 200%;
  }
}
@keyframes change-background {
  0%,
  100% {
    background-image: url("../images/discount-background1.jpg");
    background-size: cover;
  }
  50% {
    background-image: url("../images/discount-background2.jpg");
    background-size: cover;
  }
}
/* ------------- End Animation --------------*/
