// Here you can create and customize the sections' header:

@use "../globals" as *;
@use "../helpers" as *;

%section_header {
  text-align: center;
  h2 {
    position: relative;
    font-weight: normal;
    font-size: rem(40);
    margin-bottom: $txt-margin-md;
    text-transform: uppercase;
    &::before {
      content: "";
      position: absolute;
      width: rem(120);
      height: rem(2);
      bottom: rem(-30);
      left: 50%;
      transform: translateX(-50%);
      background-color: #333;
    }
    &::after {
      content: "";
      position: absolute;
      bottom: rem(-38);
      height: rem(14);
      width: rem(14);
      left: 50%;
      transform: translateX(-50%);
      border-radius: 50%;
      background-color: #fff;
      border: 2px solid #333;
    }
  }
  p {
    line-height: $line-height;
    margin: 0 auto rem(75) auto;
    color: $txt_clr;
    max-width: 100%;
    width: rem(550);
    font-size: rem(16);
  }
}

%main_title {
  position: relative;
  z-index: 2;
  width: fit-content;
  font-size: 30px;
  text-align: center;
  margin: 0 auto 100px;
  padding: 10px 20px;
  transition: $transition;
  border: 2px solid black;
  text-transform: uppercase;
  &::before, &::after {
    content: "";
    z-index: -1;
    position: absolute;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    top: 50%;
    transform: translateY(-50%);
    transition: $transition;
    background-color: $main-clr;
  }
  &::before {
    left: -30px;
  }
  &::after {
    right: -30px;
  }
  &:hover::before {
    animation: move-left .5s linear forwards ;
  }
  &:hover::after {
    animation: move-right .5s linear forwards ;
  }
  &:hover {
    border-color: #fff;
    color: white;
    transition-delay: 0.5s;
  }
}
